import React, { useEffect, useState } from 'react';
import PieChart from "../components/PieChart"
import LineChart from "../components/LineChart"
import BarChart from "../components/BarChart"

import Logo from "../components/common/Logo"
import { defaultStats, StatsType } from '../utils/types';
import Sidebar from '../components/Sidebar';

interface DashboardProps { };

const Dashboard: React.FC<DashboardProps> = () => {
    const PORT = 8081;
    const URL = `ws://127.0.0.1:${PORT}`;

    // WebSocket listener
    const [newStats, setNewStats] = useState<StatsType>(defaultStats); // Store the incoming message from WebSocket
    let [curStats, setCurStats] = useState<StatsType>(defaultStats); // Store the stats data from the various WebSocket Clients

    // WebSocket connection setup
    useEffect(() => {
        // Replace with your WebSocket server URL
        const socket = new WebSocket(URL);

        // WebSocket open event
        socket.onopen = () => {
            console.log('WebSocket connected');
        };

        // WebSocket message event
        socket.onmessage = (event) => {
            const data = event.data;
            setNewStats(JSON.parse(data));
            console.log('Received message:', newStats);
        };

        // WebSocket error event
        socket.onerror = (error) => {
            console.error('WebSocket error:', error);
        };

        // WebSocket close event
        socket.onclose = () => {
            console.log('WebSocket connection closed');
        };

        // Clean up WebSocket connection on component unmount
        return () => {
            socket.close();
        };
    }, []);

    useEffect(() => {
        let stats: StatsType = { ...curStats };
        if (stats.messagesSent != newStats.messagesSent) stats.messagesSent = newStats.messagesSent;
        if (stats.fragmentsSent != newStats.fragmentsSent) stats.fragmentsSent = newStats.fragmentsSent;
        if (stats.messagesReceived != newStats.messagesReceived) stats.messagesReceived = newStats.messagesReceived;
        if (stats.fragmentsReceived != newStats.fragmentsReceived) stats.fragmentsReceived = newStats.fragmentsReceived;
        if (stats.acksSent != newStats.acksSent) stats.acksSent = newStats.acksSent;
        if (stats.acksReceived != newStats.acksReceived) stats.acksReceived = newStats.acksReceived;
        if (stats.nacksReceived != newStats.nacksReceived) stats.nacksReceived = newStats.nacksReceived;
        setCurStats(stats);
    }, [newStats]);

    return (
        <div className="flex flex-1 bg-gray-50">
            <div className="hidden md:flex md:w-64 md:flex-col">
                <div className="flex flex-col flex-grow pt-5 overflow-y-auto bg-white">
                    <Sidebar />
                </div>
            </div>

            <div className="flex flex-col flex-1">
                <main>
                    <div className="py-6">
                        <div className="px-4 mx-auto max-w-7xl sm:px-6 md:px-8">
                            <PieChart stats={curStats} />
                            <BarChart stats={curStats} />
                        </div>
                    </div>
                </main>
            </div>
        </div>
    );
};

export default Dashboard;
